<template>
  <div style="width: 100%;height: 100%">
    <el-dialog v-model="addDialogVisible" title="添加到书架">
        <el-checkbox
          v-for="item in addDialogData.shelves"
          v-model="item.select"
          :label="item.name"
          :key="item.id"
          style="display:block;" size="large"/>
      <el-divider />
      <el-button style="width:100%;height:50px" @click.stop="addNewShelf" v-show="addNewShelfButtonShow">新建书架</el-button>
       <el-input
          v-model="newBookshelfname"
          placeholder="请输入书架名"
          size="large"
          v-if="!addNewShelfButtonShow"
        >
          <template #append>
            <el-button @click="addNewshelfConfirm">确定</el-button>
          </template>
        </el-input>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="addDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="addBookToShelfConfirm">确认</el-button>
        </span>
      </template>
    </el-dialog>
      <el-card class="box-card">
        <template #header>
          <div class="card-header">
            <div style="font-size:5px">{{book.category}}</div>
            <span style="font-weight:bold;font-size:50px">{{book.bookname}}</span>
            <span style="margin-left:20px;font-size:25px">{{book.author}}</span>
          </div>
        </template>
        <div>{{book.press}}</div>
        <div>{{book.publicationdate}}</div>
        <el-divider />
        <el-button @click="addBookToShelf">
          加入书架
        </el-button>
      </el-card>
      <el-tabs type="border-card">
        <el-tab-pane label="馆藏信息">
          <el-table :data="holdingInformation">
            <el-table-column prop="booknum" label="索书码"></el-table-column>
            <el-table-column prop="barcode" label="条码号"></el-table-column>
            <el-table-column prop="position" label="馆藏地"></el-table-column>
            <el-table-column label="图书状态">
              <template #default="scope">
                <div>
                  {{scope.row.state==1?"在架":"已借出"}}
                </div>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="图书信息">
          <el-descriptions
            :column="4"
            :size="size"
            direction="vertical"
            :style="blockMargin"
            border
          >
            <el-descriptions-item label="题名">{{book.bookname}}</el-descriptions-item>
            <el-descriptions-item label="作者">{{book.author}}</el-descriptions-item>
            <el-descriptions-item label="出版社">{{book.press}}</el-descriptions-item>
            <el-descriptions-item label="出版日期">{{book.publicationdate}}</el-descriptions-item>
            <el-descriptions-item label="版本">{{book.version}}</el-descriptions-item>
            <el-descriptions-item label="语言">{{book.language}}</el-descriptions-item>
          </el-descriptions>
        </el-tab-pane>
        <el-tab-pane label="图书简介"> {{book.introduction}}</el-tab-pane>
        <el-tab-pane label="相关阅读">
          <el-table @row-click="goToDetail" :data="relatedBook" stripe border style="width: 100%">
            <el-table-column prop="bookname" label="图书名" sortable/>
            <el-table-column prop="author" label="作者"/>
            <el-table-column prop="press" label="出版社"/>
            <el-table-column prop="category" label="类型"/>
          </el-table>
        </el-tab-pane>
      </el-tabs>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from 'axios';
import qs from 'qs';
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data() {
    return {
      userid:undefined,
      book:{},
      relatedBook:[],
      holdingInformation:[],
      addDialogData:{
        shelves:[]
      },
      addDialogVisible:undefined,
      addNewShelfButtonShow:true,
      newBookshelfname:undefined
    }
  },
  methods:{
    goToDetail(row){
      window.open("https://localhost/bookDetail?keyword="+encodeURI(row.bookid));
    },
    addNewshelfConfirm(){
      axios.post("https://localhost/bookshelf/addNewBookShelf",qs.stringify({bookShelfname:this.newBookshelfname,studentid:this.userid})).then(res=>{
        if(res.data == true){
          this.$message({
            type:"success",
            message:"添加成功"
          })
          this.addNewShelfButtonShow = true;
          axios.post("https://localhost/bookshelf/getAllBookshelfByStudentid",qs.stringify({id:this.userid})).then(res=>{
            this.addDialogData.shelves = res.data;
            this.addDialogData.shelves.forEach(element=>{
              axios.post("https://localhost/bookshelf/ifBookExistInShelf",qs.stringify({bookid:this.book.bookid,bookshelfid:element.id})).then(res=>{
                if(res.data == false)
                  element["select"] = false;
                else
                  element["select"] = true;
              })
            })
          })
        }
        else
          this.$message({
            type:"error",
            message:"添加失败"
          })
      })
    },
    addBookToShelf(){
      axios.post("https://localhost/bookshelf/getAllBookshelfByStudentid",qs.stringify({id:this.userid})).then(res=>{
        this.addDialogData.shelves = res.data;
        this.addDialogData.shelves.forEach(element=>{
          axios.post("https://localhost/bookshelf/ifBookExistInShelf",qs.stringify({bookid:this.book.bookid,bookshelfid:element.id})).then(res=>{
            if(res.data == false)
              element["select"] = false;
            else
              element["select"] = true;
          })
        })
        this.addDialogVisible = true;
      })
    },
    addNewShelf(){
      this.addNewShelfButtonShow = false;
    },
    addBookToShelfConfirm(){
      let selectedShelves = [];
      let notselectedShelves = []
      this.addDialogData.shelves.forEach(element=>{
        if(element.select == true)
        {
          selectedShelves.push(element.id);
        }
        else if(element.select == false)
        {
          notselectedShelves.push(element.id);
        }
      })
      axios.post("https://localhost/bookshelf/addBookToShelves",qs.stringify({bookid:this.book.bookid,selectedShelfids:selectedShelves,selectedLength:selectedShelves.length,notselectedShelfids:notselectedShelves,notselectedLength:notselectedShelves.length}))
      this.$message({
        type:"success",
        message:"添加成功"
      })
      this.addDialogVisible = false;
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    this.userid = token.id;
    this.searchInput = decodeURI(window.location.search.slice(9));
    axios.post("https://localhost/bookshelf/getAllBookshelfByStudentid",qs.stringify({id:this.userid})).then(res=>{
      this.addDialogData.shelves = res.data;
      this.addDialogData.shelves.forEach(element=>{
        axios.post("https://localhost/bookshelf/ifBookExistInShelf",qs.stringify({bookid:this.book.bookid,bookshelfid:element.id})).then(res=>{
          if(res.data == false)
            element["select"] = false;
          else
            element["select"] = true;
        })
      })
    })
    axios.post('https://localhost/book/getBookById',qs.stringify({bookid:this.searchInput})).then(res=>{
      this.book = res.data;
      this.book.publicationdate = Number(this.book.publicationdate.substring(0,4))+1;
      axios.post('https://localhost/book/getRelatedBook',qs.stringify({category:this.book.category})).then(res=>{
        this.relatedBook = res.data;
      });
      axios.post('https://localhost/holdingInformation/getHoldingInformationByBookid',qs.stringify({bookid:this.searchInput})).then(res=>{
        this.holdingInformation = res.data;
      })
    })
  }
}
</script>
